import React, { useState } from "react";
import { Carousel } from 'react-responsive-carousel';

import expand from "../../assets/images/Expand_right_light.png";
import imageProduct from "../../assets/images/image 35.png";
import Groupfav from "../../assets/images/Groupfav.png";
import Ellipse2 from "../../assets/images/Ellipse 2.png";
import Group9 from "../../assets/images/Group 9.png";
import image51 from "../../assets/images/image 51.png";
import image35 from "../../assets/images/image 35.png";

import { useTranslation } from 'react-i18next';
import { Modal, Button } from "antd";
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import 'swiper/css';
import ChatBox from "../../components/common/chatBox";
import VFM_Avatar from "../../components/common/avatar";

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '1px solid #6F6F6F',
    boxShadow: 24,
    p: 4,
};

const Requester = () => {
    const { t } = useTranslation();
    const [open, setOpen] = useState(false);
    const [chatOption, setChatOption] = useState(false);

    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    return (
        <>
            {/* <Header page="webscreens" pagetitle="REQUESTER" /> */}
            <div className="py-24 xl:px-20 md:px-10 px-5">
                <section id="product-title">
                    <p className="flex gap-1 text-sm mb-3">{t("REQUESTER")}
                        <img className="w-5" src={expand} alt="expand" />
                    </p>
                    <div className="bg-blue-600 text-white px-5 py-1 font-medium rounded-md text-sm">
                        <p className="p-4 text-xl">■REQUEST■ @REQUEST＿USER000</p>
                    </div>
                </section>
                <section id="carousel-box">
                    <div className="flex flex-col xl:flex-row  mb-10">
                        <div className="xl:w-1/2">
                            <Carousel className="carousel-product mt-24">
                                {[imageProduct, imageProduct, imageProduct].map((image, index) => (
                                    <div key={index}>
                                        <img className="main-img-carousel" src={image} alt="product" />
                                    </div>
                                ))}
                            </Carousel>
                        </div>
                        <div className="py-24 xl:w-1/2">
                            <div className=" rounded-lg border-gray-500 shadow-lg xl:mx-10 md:mx-5 mx-0 p-7 shadow-gray-500 border-none">
                                <div className="flex flex-col items-center justify-start">
                                    <VFM_Avatar size={32} img={Ellipse2} />
                                    <h1 className="text-badge my-2">DESIGNER</h1>
                                    <h1 >Kaiyo Aoki [ Eclipse of Elegance ]</h1>
                                </div>
                                <div className="mt-5 flex flex-col gap-5">
                                    <p className="flex">【イメージ・モチーフ】サイバーパンク、GAL、メランコリック</p>
                                    <p className="flex">【カテゴリー】ドレスのみまたは全身のコーディネート</p>
                                    <p className="flex">【指定】キラキラする装飾を入れていただけますでしょうか。</p>
                                    <p className="flexflex">【予算】上半身・全身１枚ずつ　動画なし　＄400.00</p>
                                    <p className=""> 以上の内容でお願いします。指定していない部分はお任せします。...続きを見る</p>
                                </div>
                                <p onClick={handleOpen} className="text-blue-700 cursor-pointer mt-4 mb-4">{t("Read more ...")}</p>
                                <div className="flex pl-8 font-bold gap-9 justify-between mt-3">
                                    <p className="w-36 border-b-4 border-blue-700">料金</p>
                                    <p className="w-36">＄920.00 USD</p>
                                </div>
                                <div className="flex pl-8 font-bold gap-9 justify-between my-3">
                                    <p className="w-36 border-b-4 border-blue-700">料金</p>
                                    <p className="w-36">＄920.00 USD</p>
                                </div>
                                <div className="flex pl-7 font-bold gap-9 justify-between">
                                    <p className="w-36">オプション</p>
                                    <p className="w-36">６点</p>
                                </div>
                                <button onClick={() => setChatOption(true)} className="px-20 bg-badge text-white w-full py-3 rounded-md mt-5 hover:opacity-40 duration-500">{t("Apply for request")}</button>
                            </div>
                        </div>
                    </div>
                    {chatOption && (
                        <ChatBox />
                    )}
                </section >
            </div >
            <Modal open={open} onCancel={handleClose} footer={
                <Button type="primary" onClick={handleClose}>  Cancel
                </Button>
            } >
                <div>
                    <div className="border-solid border-gray-500 mx-auto p-5">
                        <div className="justify-center items-center text-center">
                            <VFM_Avatar size={32} img={Ellipse2} />
                            <h1 className="text-badge my-7">DESIGNER</h1>
                            <h1>Kaiyo Aoki [ Eclipse of Elegance ]</h1>

                        </div>
                        <div className="my-5 flex flex-col gap-5">
                            <p className="flex">【イメージ・モチーフ】サイバーパンク、GAL、メランコリック</p>
                            <p className="flex">【カテゴリー】ドレスのみまたは全身のコーディネート</p>
                            <p className="flex">【指定】キラキラする装飾を入れていただけますでしょうか。</p>
                            <p className="flexflex">【予算】上半身・全身１枚ずつ　動画なし　＄400.00</p>
                            <p className=""> 以上の内容でお願いします。指定していない部分はお任せします。...続きを見る</p>
                        </div>
                    </div>
                </div>
            </Modal >
        </>
    );
}

export default Requester;
